@import "../../variables.scss";

.wrapper {
  margin-left: 220px;
  margin-right: 220px;
  margin-top: 60px;
}

.content {
  margin-bottom: 120px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(396px, 1fr));
  grid-gap: 35px;
}

.contacts {
  display: flex;
  justify-content: space-between;
}
